const demos = '<iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3966.1317487605083!2d106.79141831477304!3d-6.246363662901295!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x2e69f113286b1dd9%3A0x809586ad22c6f374!2sWisma%20Bayuadji!5e0!3m2!1sen!2sid!4v1571377559570!5m2!1sen!2sid" width="600" height="450" frameborder="0" style="border:0;" allowfullscreen=""></iframe>';

