import { Link } from 'react-router-dom';
import avartar1 from "../../../images/avatar/natadana/panduan.png";
import avartar2 from "../../../images/avatar/natadana/about.png";
import avartar3 from "../../../images/avatar/natadana/10.png";
import SopPDF from '../../../files/sop.pdf';



const Cards = () => {
	return(
		<>
			 <div className="col-xl-3 col-xxl-4 col-sm-4">
				<Link to="/Superadmin" className="card bg-secondary invoice-card">
					<div className="card-body d-flex">
						<div className="icon me-3">
							<img className=" img-fluid" src={avartar1} width="40" alt=""/>
						</div>
						<div>
							<span className="text-white fs-18">Panduan Pengisian</span>
						</div>
					</div>
				</Link>
			</div>
			<div className="col-xl-3 col-xxl-4 col-sm-4">
				<Link to="/tentang-fidusia" className="card bg-secondary invoice-card">
					<div className="card-body d-flex">
						<div className="icon me-3">
							<img className=" img-fluid" src={avartar2} width="40" alt=""/>
						</div>
						<div>
							<span className="text-white fs-18">Tentang Fidusia</span>

						</div>
					</div>
				</Link>
			</div>
			<div className="col-xl-3 col-xxl-4 col-sm-4">
				<Link to={SopPDF}
					download="SOP-PDF-document"
					target="_blank"
					rel="noreferrer"
					className="card bg-secondary invoice-card">
					<div className="card-body d-flex">
						<div className="icon me-3">
							<img className=" img-fluid" src={avartar3} width="40" alt=""/>

						</div>
						<div>
							<span className="text-white fs-18">SOP</span>
						</div>
					</div>
				</Link>
			</div>
		</>	
	)
} 
export default Cards;