import React, { Fragment, useContext, useState } from "react";
/// React router dom
import { Link } from "react-router-dom";
import { ThemeContext } from "../../../context/ThemeContext";
import accLogos from "../../../images/natadanas/logo_acc.png";
import accLogos2 from "../../../images/iadmin.png";



const NavHader = () => {
  const [toggle, setToggle] = useState(false);
  const { navigationHader, openMenuToggle, background } = useContext(
    ThemeContext
  );
  return (
    <div className="nav-header" style={{height:"7rem"}}>
      <Link to="/dashboard" className="brand-logo">
        {background.value === "dark" || navigationHader !== "color_1" ? (
          <Fragment>
            <img className="logo-abbr me-2" src={accLogos} style={{maxWidth:"85px",borderRadius:"10%"}}/>

          </Fragment>
        ) : (
          <Fragment>
          <img className="logo-abbr me-2" src={accLogos2} style={{maxWidth:"85px",borderRadius:"10%"}}/>

          </Fragment>
        )}
      </Link>

      <div
        className="nav-control"
        onClick={() => {
          setToggle(!toggle);
          openMenuToggle();
        }}
      >
        <div className={`hamburger ${toggle ? "is-active" : ""}`}>
          <span className="line"></span>
          <span className="line"></span>
          <span className="line"></span>
        </div>
      </div>
    </div>
  );
};

export default NavHader;
