import React, { Fragment } from "react";
import { ButtonGroup, Dropdown, SplitButton } from "react-bootstrap";
import DropdownMultiselect from "react-multiselect-dropdown-bootstrap";
import PageTItle from "./layouts/PageTitle";
import { RangeDatePicker, DatePicker } from "@y0c/react-datepicker";


const Element = () => {
  return (
    <Fragment>
      {/* <PageTItle activeMenu="Element" motherMenu="Form" pageContent="Element" /> */}

      <div className="row">
        <div className="col-xl-6 col-lg-6">
          <div className="card">
            <div className="card-header">
              <p className="card-title">Perjanjian Pembiayaan</p>
            </div>
            <div className="card-body">
              <div className="basic-form">
                <form onSubmit={(e) => e.preventDefault()}>
                  <div className="form-group mb-0">
                    <h5 className="">Jenis Penggunaan</h5>
                    
                    <div className="radio">
                      <label>
                        <input type="radio" name="optradio" /> Produktif
                      </label>
                    </div>
                    <div className="radio">
                      <label>
                        <input type="radio" name="optradio" /> Konsumtif
                      </label>
                    </div>
                  </div>
                  <div className="input-group my-3">
                    <span className="input-group-text col-4">Tanggal Surat Kuasa</span>

                    <DatePicker className="col-8"/>
                  </div>
                  <div className="input-group my-3">
                    <span className="input-group-text col-4">Berdasarkan Perjanjian</span>

                    <input
                      type="text"
                      className="form-control col-8"
                    />
                  </div>
                  <div className="input-group my-3">
                    <span className="input-group-text col-4">Nomor Kontrak</span>
                    
                    <input type="text" className="form-control col-8" />
                  </div>
                  <div className="input-group my-3">
                    <span className="input-group-text col-4">Tanggal Awal Perjanjian</span>

                    <DatePicker className="col-8"/>
                  </div>
                  <div className="input-group my-3">
                    <span className="input-group-text col-4">Tanggal Akhir Perjanjian</span>

                    <DatePicker className="col-8"/>
                  </div>
                  <div className="input-group my-3">
                    <span className="input-group-text col-4">Nilai Penjamin Fidusia</span>
                    
                    <input type="text" className="form-control col-8" />
                  </div>
                  <div className="input-group my-3">
                    <span className="input-group-text col-4">Nilai PNBP</span>
                    
                    <input type="text" className="form-control col-8" />
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
        <div className="col-xl-6 col-lg-6">
          <div className="card">
            <div className="card-header">
              <h4 className="card-title">Objek Penjaminan</h4>
            </div>
            <div className="card-body">
              <div className="input-group my-3">
                <span className="input-group-text col-4">Merek</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Tipe</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Jenis Model</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Tahun Pembuatan</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Nomor Rangka</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Nomor Mesin</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Jumlah Roda</span>

                <select
                  defaultValue={"-"}
                  className="form-control form-control-lg col-8"
                >
                  <option>-</option>
                  <option>2</option>
                  <option>4</option>
                  <option>6</option>
                </select>
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Pilih Bukti Objek</span>

                <select
                  defaultValue={"-"}
                  className="form-control form-control-lg col-8"
                >
                  <option>-</option>
                  <option>Buku Pemilik Kendaraan Bermotor</option>
                  <option>Surat Pernyataan</option>
                  <option>Cover Note</option>
                </select>
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Atas Nama</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Nilai Objek</span>
                
                <input type="text" className="form-control col-8" />
              </div>
            </div>
          </div>
        </div>
        <div className="col-xl-6 col-lg-6">
          <div className="card">
            <div className="card-header">
              <h4 className="card-title">Pemberi Fidusia</h4>
            </div>
            <div className="card-body">
              <div className="input-group my-3">
                <span className="input-group-text col-4">Penggilan</span>

                <select
                  defaultValue={"-"}
                  className="form-control form-control-lg col-8"
                >
                  <option>Tuan</option>
                  <option>Bapak</option>
                  <option>Ibu</option>
                  <option>Nyonya</option>
                </select>
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Nama</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Tempat Lahir</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Tanggal Lahir</span>
                
                <DatePicker className="col-8"/>
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Pekerjaan</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Alamat</span>
                <textarea
                      className="form-control"
                      rows="4"
                      id="comment"
                    ></textarea>
                {/* <input type="text" className="form-control col-8" /> */}
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Kode Pos</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">RT</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">RW</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Kelurahan</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Kecamatan</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Kabupaten</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Provinsi</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">NIK</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">No Telp</span>
                
                <input type="text" className="form-control col-8" />
              </div>
            </div>
          </div>
        </div>
        <div className="col-xl-6 col-lg-6">
          <div className="card">
            <div className="card-header">
              <h4 className="card-title">Pemberi Fidusia (*Harus diisi jika ada persetujuan pasangan)</h4>
            </div>
            <div className="card-body">
              <div className="input-group my-3">
                <span className="input-group-text col-4">Nama</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Tempat Lahir</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Tanggal Lahir</span>
                
                <DatePicker className="col-8"/>
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Pekerjaan</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">NIK</span>
                
                <input type="text" className="form-control col-8" />
              </div>
            </div>
          </div>
        </div>
        <div className="col-xl-6 col-lg-6">
          <div className="card">
            <div className="card-header">
              <h4 className="card-title">Debitur (*diisi jika penerima fasilitas berbeda dengan pemberi)</h4>
            </div>
            <div className="card-body">
              <div className="input-group my-3">
                <span className="input-group-text col-4">Penggilan</span>

                <select
                  defaultValue={"-"}
                  className="form-control form-control-lg col-8"
                >
                  <option>Tuan</option>
                  <option>Bapak</option>
                  <option>Ibu</option>
                  <option>Nyonya</option>
                </select>
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Nama</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Tempat Lahir</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Tanggal Lahir</span>
                
                <DatePicker className="col-8"/>
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Pekerjaan Debitur</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Alamat</span>
                <textarea
                      className="form-control"
                      rows="4"
                      id="comment"
                    ></textarea>
                {/* <input type="text" className="form-control col-8" /> */}
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Kode Pos</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">RT</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">RW</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Kelurahan Debitur</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Kecamatan Debitur</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Kabupaten Debitur</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">Provinsi Debitur</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">NIK</span>
                
                <input type="text" className="form-control col-8" />
              </div>
              <div className="input-group my-3">
                <span className="input-group-text col-4">No Telp</span>
                
                <input type="text" className="form-control col-8" />
              </div>
            </div>
          </div>
        </div>
        <div className="col-xl-6 col-lg-6">
          <div className="card">
            <div className="card-header">
              <h4 className="card-title">File Scan Upload</h4>
            </div>
            <div className="card-body">
              <div className="input-group mb-3">
                <span className="input-group-text col-3">File Scan</span>
                <div className="form-file col-8">
                  <input type="file" className="custom-file-input form-control" />
                  
                </div>
              </div>

              <div className="input-group mb-3">
                <span className="input-group-text col-3">File Scan Tambahan</span>
                <div className="form-file col-8">
                  <input type="file" className="custom-file-input form-control" />
                  
                </div>

              </div>

            </div>
            <div className="card-footer">
              <button className="btn btn-primary" type="button">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default Element;
