import React,{useState, useContext, useEffect} from 'react';
import {Link} from 'react-router-dom';
import loadable from "@loadable/component";
import pMinDelay from "p-min-delay";
import { ThemeContext } from "../../context/ThemeContext";
import { RangeDatePicker, DatePicker } from "@y0c/react-datepicker";

//Images
import pattern6 from '../../images/pattern/pattern6.png';
import circle from '../../images/pattern/circle.png';
//Components
import DropdownBlog from '../components/Dompet/DropdownBlog';
import PreviousTab from '../components/Dompet/Home/PreviousTab';
import Cards from './components/Cards';
import Form from './components/Form'
import MetarialDate from "../components/Forms/Pickers/MetarialDate";


const PolarChart = loadable(() =>
	pMinDelay(import("../components/Dompet/Home/PolarChart"), 1000)
);
const ActivityApexBarGraph = loadable(() =>
	pMinDelay(import("../components/Dompet/Home/ActivityApexBarGraph"), 1000)
);
const TransactionApexBar = loadable(() =>
	pMinDelay(import("../components/Dompet/Home/TransactionApexBar"), 1000)
);


const Home = () => {
	const { changeBackground, background } = useContext(ThemeContext);
	useEffect(() => {
		changeBackground({ value: "light", label: "Light" });
	}, []);
	
	//Checkbox
	const [checked, setChecked] = useState(true);
	return(
		<>
			<div className="row invoice-card-row">
				<Form />
			</div>	
			<div className="row invoice-card-row">
				<Cards />
			</div>	
		</>
	)
}
export default Home;