/// Charts
import SparklineChart from "./components/charts/Sparkline";
import ChartJs from "./components/charts/Chartjs";
import Chartist from "./components/charts/chartist";
import RechartJs from "./components/charts/rechart";
import ApexChart from "./components/charts/apexcharts";

/// Bootstrap
import UiAlert from "./components/bootstrap/Alert";
import UiAccordion from "./components/bootstrap/Accordion";
import UiBadge from "./components/bootstrap/Badge";
import UiButton from "./components/bootstrap/Button";
import UiModal from "./components/bootstrap/Modal";
import UiButtonGroup from "./components/bootstrap/ButtonGroup";
import UiListGroup from "./components/bootstrap/ListGroup";
import UiMediaObject from "./components/bootstrap/MediaObject";
import UiCards from "./components/bootstrap/Cards";
import UiCarousel from "./components/bootstrap/Carousel";
import UiDropDown from "./components/bootstrap/DropDown";
import UiPopOver from "./components/bootstrap/PopOver";
import UiProgressBar from "./components/bootstrap/ProgressBar";
import UiTab from "./components/bootstrap/Tab";
import UiPagination from "./components/bootstrap/Pagination";
import UiGrid from "./components/bootstrap/Grid";
import UiTypography from "./components/bootstrap/Typography";

/// Plugins
import Select2 from "./components/PluginsMenu/Select2/Select2";
import Nestable from "./components/PluginsMenu/Nestable/Nestable";
import MainNouiSlider from "./components/PluginsMenu/Noui Slider/MainNouiSlider";
import MainSweetAlert from "./components/PluginsMenu/Sweet Alert/SweetAlert";
import Toastr from "./components/PluginsMenu/Toastr/Toastr";
import JqvMap from "./components/PluginsMenu/Jqv Map/JqvMap";
import Lightgallery from "./components/PluginsMenu/Lightgallery/Lightgallery";

//Redux
import Todo from "./pages/Todo";
import ReduxForm from "./components/Forms/ReduxForm/ReduxForm";
import WizardForm from "./components/Forms/ReduxWizard/Index";

/// Widget
import Widget from "./pages/Widget";

/// Table
import SortingTable from "./components/table/SortingTable/SortingTable";
import FilteringTable from "./components/table/FilteringTable/FilteringTable";
import DataTable from "./components/table/DataTable";
import BootstrapTable from "./components/table/BootstrapTable";

/// Form
import Element from "./components/Forms/Element/Element";
import Wizard from "./components/Forms/Wizard/Wizard";
import SummerNote from "./components/Forms/Summernote/SummerNote";
import Pickers from "./components/Forms/Pickers/Pickers";
import jQueryValidation from "./components/Forms/jQueryValidation/jQueryValidation";

/// Pages
import Registration from "./pages/Registration";
import Login from "./pages/Login";
import ForgotPassword from "./pages/ForgotPassword";
import LockScreen from "./pages/LockScreen";
import Error400 from "./pages/Error400";
import Error403 from "./pages/Error403";
import Error404 from "./pages/Error404";
import Error500 from "./pages/Error500";
import Error503 from "./pages/Error503";
import Setting from "./layouts/Setting";
import { ThemeContext } from "../context/ThemeContext";

//superadmin
import SuperHome from "./superadmin/Home"
import SuperDataAll from "./superadmin/datatable/all"
import SuperDataMasuk from "./superadmin/datatable/terupload"
import SuperDataMasukUrlAda from "./superadmin/datatable/terupload_url_ada"
import SuperDataMasukTidakAdaUrl from "./superadmin/datatable/terupload_url_tidak"
import SuperDataSertifikat from "./superadmin/datatable/datasertifikat"
import SuperDataReject from "./superadmin/datatable/reject"

import SuperReportMenu from "./superadmin/menu_report"
import SuperSetting from "./superadmin/setting"
import SuperHelpDesk from "./superadmin/contactUs"
import SuperInformasi from "./superadmin/informasi"

//pusat
import PusatHome from "./pusat/Home"
import PusatDataAll from "./pusat/datatable/all"
import PusatDataMasuk from "./pusat/datatable/terupload"
import PusatDataMasukUrlAda from "./pusat/datatable/terupload_url_ada"
import PusatDataMasukTidakAdaUrl from "./pusat/datatable/terupload_url_tidak"
import PusatDataSertifikat from "./pusat/datatable/datasertifikat"
import PusatDataReject from "./pusat/datatable/reject"

import PusatReportMenu from "./pusat/menu_report"
import PusatSetting from "./pusat/setting"
import PusatHelpDesk from "./pusat/contactUs"
import PusatInformasi from "./pusat/informasi"

//notaris
import NotarisHome from "./notaris/Home"
import NotarisDataAll from "./notaris/datatable/all"
import NotarisDataMasuk from "./notaris/datatable/terupload"
import NotarisDataMasukUrlAda from "./notaris/datatable/terupload_url_ada"
import NotarisDataMasukTidakAdaUrl from "./notaris/datatable/terupload_url_tidak"
import NotarisDataSertifikat from "./notaris/datatable/datasertifikat"
import NotarisDataReject from "./notaris/datatable/reject"

import NotarisReportMenu from "./notaris/menu_report"
import NotarisSetting from "./notaris/setting"
import NotarisHelpDesk from "./notaris/contactUs"
import NotarisInformasi from "./notaris/informasi"

//user
import UserHome from "./user/Home"
import UserDataAll from "./user/datatable/all"
import UserDataMasuk from "./user/datatable/terupload"
import UserDataMasukUrlAda from "./user/datatable/terupload_url_ada"
import UserDataMasukTidakAdaUrl from "./user/datatable/terupload_url_tidak"
import UserDataSertifikat from "./user/datatable/datasertifikat"
import UserDataReject from "./user/datatable/reject"

import Userinvoice from "./user/components/laporanTable"
import UserSetting from "./user/setting"
import UserHelpDesk from "./user/contactUs"
import UserInformasi from "./user/informasi"

//universal
import tentangFidusia from "./tentang_fidusia"
import AddData from "./add_data"





const Markup = () => {
  const { menuToggle } = useContext(ThemeContext);
  const routes = [
    /// Dashboard
    { url: "", component: SuperHome },
    { url: "dashboard", component: Home },
    { url: "dashboard-dark", component: DashboardDark },
    { url: "my-wallet", component: MyWallet },
    { url: "invoices", component: Invoices },
    { url: "cards-center", component: CardsCenter },
    { url: "transaction-history", component: Transaction },
    { url: "transaction-details", component: TransactionDetails },

    /// Apps
    { url: "app-profile", component: AppProfile },
    { url: "email-compose", component: Compose },
    { url: "email-inbox", component: Inbox },
    { url: "email-read", component: Read },
    { url: "app-calender", component: Calendar },
    { url: "post-details", component: PostDetails },

    /// Chart
    { url: "chart-sparkline", component: SparklineChart },
    { url: "chart-chartjs", component: ChartJs },
    { url: "chart-chartist", component: Chartist },
    { url: "chart-apexchart", component: ApexChart },
    { url: "chart-rechart", component: RechartJs },

    /// Bootstrap
    { url: "ui-alert", component: UiAlert },
    { url: "ui-badge", component: UiBadge },
    { url: "ui-button", component: UiButton },
    { url: "ui-modal", component: UiModal },
    { url: "ui-button-group", component: UiButtonGroup },
    { url: "ui-accordion", component: UiAccordion },
    { url: "ui-list-group", component: UiListGroup },
    { url: "ui-media-object", component: UiMediaObject },
    { url: "ui-card", component: UiCards },
    { url: "ui-carousel", component: UiCarousel },
    { url: "ui-dropdown", component: UiDropDown },
    { url: "ui-popover", component: UiPopOver },
    { url: "ui-progressbar", component: UiProgressBar },
    { url: "ui-tab", component: UiTab },
    { url: "ui-pagination", component: UiPagination },
    { url: "ui-typography", component: UiTypography },
    { url: "ui-grid", component: UiGrid },

    /// Plugin
    { url: "uc-select2", component: Select2 },
    { url: "uc-nestable", component: Nestable },
    { url: "uc-noui-slider", component: MainNouiSlider },
    { url: "uc-sweetalert", component: MainSweetAlert },
    { url: "uc-toastr", component: Toastr },
    { url: "map-jqvmap", component: JqvMap },
    { url: "uc-lightgallery", component: Lightgallery },

	///Redux
	{ url: "todo", component: Todo },
	{ url: "redux-form", component: ReduxForm },
    { url: "redux-wizard", component: WizardForm },
	
    /// Widget
    { url: "widget-basic", component: Widget },

    /// Shop
    { url: "ecom-product-grid", component: ProductGrid },
    { url: "ecom-product-list", component: ProductList },
    { url: "ecom-product-detail", component: ProductDetail },
    { url: "ecom-product-order", component: ProductOrder },
    { url: "ecom-checkout", component: Checkout },
    { url: "ecom-invoice", component: Invoice },
    { url: "ecom-product-detail", component: ProductDetail },
    { url: "ecom-customers", component: Customers },

    /// Form
    { url: "form-element", component: Element },
    { url: "form-wizard", component: Wizard },
    { url: "form-editor-summernote", component: SummerNote },
    { url: "form-pickers", component: Pickers },
    { url: "form-validation-jquery", component: jQueryValidation },

    /// table
	{ url: 'table-filtering', component: FilteringTable },
    { url: 'table-sorting', component: SortingTable },
    { url: "table-datatable-basic", component: DataTable },
    { url: "table-bootstrap-basic", component: BootstrapTable },

    /// pages
    { url: "page-register", component: Registration },
    { url: "page-lock-screen", component: LockScreen },
    { url: "page-login", component: Login },
    { url: "page-forgot-password", component: ForgotPassword },
    { url: "page-error-400", component: Error400 },
    { url: "page-error-403", component: Error403 },
    { url: "page-error-404", component: Error404 },
    { url: "page-error-500", component: Error500 },
    { url: "page-error-503", component: Error503 },
    /// superadmin
    { url: "Superadmin", component: SuperHome },
    { url: "Superadmin/data/all", component: SuperDataAll },
    { url: "Superadmin/data/masuk", component: SuperDataMasuk },
    { url: "Superadmin/data/masuk/ada_url", component: SuperDataMasukUrlAda },
    { url: "Superadmin/data/masuk/tidak_ada_url", component: SuperDataMasukTidakAdaUrl },
    { url: "Superadmin/data/serifikat", component: SuperDataSertifikat },
    { url: "Superadmin/data/reject", component: SuperDataReject },
    { url: "add/data", component: AddData },
    { url: "Superadmin/menu/laporan", component: SuperReportMenu },
    { url: "Superadmin/menu/pengaturan", component: SuperSetting },
    { url: "Superadmin/menu/helpdesk", component: SuperHelpDesk },
    { url: "Superadmin/menu/informasi", component: SuperInformasi },

    /// pusat
    { url: "Pusat", component: PusatHome },
    { url: "Pusat/data/all", component: PusatDataAll },
    { url: "Pusat/data/masuk", component: PusatDataMasuk },
    { url: "Pusat/data/masuk/ada_url", component: PusatDataMasukUrlAda },
    { url: "Pusat/data/masuk/tidak_ada_url", component: PusatDataMasukTidakAdaUrl },
    { url: "Pusat/data/serifikat", component: PusatDataSertifikat },
    { url: "Pusat/data/reject", component: PusatDataReject },
    { url: "Pusat/menu/laporan", component: PusatReportMenu },
    { url: "Pusat/menu/pengaturan", component: PusatSetting },
    { url: "Pusat/menu/helpdesk", component: PusatHelpDesk },
    { url: "Pusat/menu/informasi", component: PusatInformasi },

    /// notaris
    { url: "Notaris", component: NotarisHome },
    { url: "Notaris/data/all", component: NotarisDataAll },
    { url: "Notaris/data/masuk", component:NotarisDataMasuk },
    { url: "Notaris/data/masuk/ada_url", component: NotarisDataMasukUrlAda },
    { url: "Notaris/data/masuk/tidak_ada_url", component: NotarisDataMasukTidakAdaUrl },
    { url: "Notaris/data/serifikat", component: NotarisDataSertifikat },
    { url: "Notaris/data/reject", component: NotarisDataReject },
    { url: "Notaris/menu/laporan", component: NotarisReportMenu },
    { url: "Notaris/menu/pengaturan", component: NotarisSetting },
    { url: "Notaris/menu/helpdesk", component: NotarisHelpDesk },
    { url: "Notaris/menu/informasi", component: NotarisInformasi },

    /// notaris
    { url: "User", component: UserHome },
    { url: "User/data/all", component: UserDataAll },
    { url: "User/data/masuk", component:UserDataMasuk },
    { url: "User/data/masuk/ada_url", component:UserDataMasukUrlAda },
    { url: "User/data/masuk/tidak_ada_url", component: UserDataMasukTidakAdaUrl },
    { url: "User/data/serifikat", component: UserDataSertifikat },
    { url: "User/data/reject", component: UserDataReject },
    { url: "User/menu/invoice", component: Userinvoice },
    { url: "User/menu/pengaturan", component: UserSetting },
    { url: "User/menu/helpdesk", component: UserHelpDesk },
    { url: "User/menu/informasi", component: UserInformasi },

    { url: "tentang-fidusia", component: tentangFidusia },
    { url: "add/data", component: AddData },













  ];
  let path = window.location.pathname;
  path = path.split("/");
  path = path[path.length - 1];

  let pagePath = path.split("-").includes("page");
  return (
    <>
      <div
        id={`${!pagePath ? "main-wrapper" : ""}`}
        className={`${!pagePath ? "show" : "mh100vh"}  ${
          menuToggle ? "menu-toggle" : ""
        }`}
      >
        {!pagePath && <Nav />}

        <div className={`${!pagePath ? "content-body" : ""}`}>
          <div
            className={`${!pagePath ? "container-fluid" : ""}`}
            style={{ minHeight: window.screen.height - 60 }}
          >
            <Switch>
              {routes.map((data, i) => (
                <Route
                  key={i}
                  exact
                  path={`/${data.url}`}
                  component={data.component}
                />
              ))}
            </Switch>
          </div>
        </div>
        {!pagePath && <Footer />}
      </div>
      <Setting />
    </>
  );
};

export default Markup;
