import React, { Fragment } from "react";
import { Link } from 'react-router-dom';
import PageTitle from "../../layouts/PageTitle";
import Form from './FormDataTable'

import {
  Row,
  Col,
  Card,
  Table,
  Badge,
  ProgressBar,
} from "react-bootstrap";


const BootstrapTable = () => {
  const chackbox = document.querySelectorAll(".bs_exam_topper input");
  const motherChackBox = document.querySelector(".bs_exam_topper_all input");
  const chackboxFun = (type) => {
    for (let i = 0; i < chackbox.length; i++) {
      const element = chackbox[i];
      if (type === "all") {
        if (motherChackBox.checked) {
          element.checked = true;
        } else {
          element.checked = false;
        }
      } else {
        if (!element.checked) {
          motherChackBox.checked = false;
          break;
        } else {
          motherChackBox.checked = true;
        }
      }
    }
  };
  const svg1 = (
    <svg width="20px" height="20px" viewBox="0 0 24 24" version="1.1">
      <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
        <rect x="0" y="0" width="24" height="24"></rect>
        <circle fill="#000000" cx="5" cy="12" r="2"></circle>
        <circle fill="#000000" cx="12" cy="12" r="2"></circle>
        <circle fill="#000000" cx="19" cy="12" r="2"></circle>
      </g>
    </svg>
  );

  return (
    <Fragment>
      {/* <PageTitle activeMenu="Table" motherMenu="Bootstrap" /> */}
      <Row>
            <Form />
      </Row>
      <Row>
        <Col lg={12}>
          <Card>
            <Card.Header>
              <Card.Title>Semua Data</Card.Title>
              <Link to="add/data" className="btn btn-primary me-1">
                <i class="las la-plus"></i>
              </Link>
            </Card.Header>
            <Card.Body>
            <div id="example_wrapper" className="dataTables_wrapper">
              <Table responsive hover>
                <thead>
                  <tr>
                    <th></th>
                    <th>No</th>
                    <th>Periode</th>
                    <th>Cabang</th>
                  </tr>
                </thead>
                <tbody>
                <tr >
                    <td className="text-center">
                      <span class="badge badge-rounded badge-success"> <i className="fa fa-download"></i></span>
                      <span class="badge badge-rounded badge-success"> <i className="fa fa-download"></i></span>

                    </td>
                    <td>0140040002070399</td>
                    <td>2021-01-07</td>
                    <td>KEDIRI</td>
                    
                </tr>
                {/* <tr>
                    <td>123</td>
                    <td>Fulan bin Fulana</td>
                    <td>1000</td>
                    <td>1000</td>
                    <td>Objek Jaminan</td>
                    <td>Tipe</td>
                    <td>Jenis/Model</td>
                    <td>Tahun Pembuatan</td>
                    <td>Nomor Rangka</td>
                    <td>Nomor Mesin</td>
                    <td>Nilai Objek</td>
                    <td>Objek Jaminan</td>
                    <td>Tanggal Masuk</td>
                    <td>Waktu perjanjian awal</td>
                    <td>Waktu perjanjian akhir</td>
                    <td>Cabang</td>
                    <td>Status</td>
                    <td></td>
                  </tr> */}
                </tbody>
                
              </Table>
              <div className="d-sm-flex text-center justify-content-between align-items-center mt-3">
                
                <div className="dataTables_info">
                  Showing of 1 entries
                </div>

                <div
                  className="dataTables_paginate paging_simple_numbers"
                  id="example5_paginate"
                >
                  <Link
                    className="paginate_button previous disabled"
                    to="#"
                    
                  >
                    <i className="fa fa-angle-double-left" aria-hidden="true"></i>
                  </Link>
                  <span>
                    
                      <Link
                        key='1'
                        to="#"
                        className={`paginate_button`}
                      >
                        1
                      </Link>
                  </span>
                  <Link
                    className="paginate_button next"
                    to="#"
                  >
                    <i className="fa fa-angle-double-right" aria-hidden="true"></i>
                  </Link>
                </div>
              </div>
              </div>
            </Card.Body>
          </Card>
          {/* <!-- /# card --> */}
        </Col>
      </Row>
    </Fragment>
  );
};

export default BootstrapTable;
